.show {
	padding: 120px 0 0;
	@include lg {
		padding: 100px 20px 0; }
	&__title {
		margin-bottom: 20px;
		max-width: 720px;
		color: $white;
		@include f('light', 42, 44);
		@include lg {
			margin-bottom: 30px;
			max-width: 440px;
			@include f(null, 50, 52); }
		@include sm {
			max-width: 340px;
			@include f(null, 32, 36); }
		em {
			display: block;
			color: $yellow;
			@include f('med'); } }
	&__decor {
		margin-bottom: 70px;
		@include lg {
			margin-bottom: 50px; } } }
