.exhibiting {
	color: $white;
	@include lg {
		font-size: 40px; }
	&_yellow {
		position: relative;
		z-index: 11; }
	&_padTop {
		margin-top: -56px;
		@include lg {
			.exhibiting__bottom {
				display: none; }
			.exhibiting__wrap:before {
				bottom: 0; }
			.exhibiting__inner {
				padding-bottom: 0; } } }
	&__wrap {
		position: relative;
		z-index: 10;
		margin: 0 auto;
		padding: 58px 66px 56px 104px;
		max-width: 980px;
		@include lg {
			padding: 40px 57px 200px 80px; }
		@include sm {
			padding: 40px 20px 200px; }
		.exhibiting_yellow & {
			padding-top: 72px;
			padding-bottom: 42px;
			color: $black;
			overflow: hidden;
			@include lg {
				padding: 60px 57px 32px 55px; }
			@include sm {
				padding: 60px 20px; }
			&:before {
				position: absolute;
				content: "";
				top: 0;
				left: 0;
				right: 0;
				bottom: 42px;
				z-index: -1;
				background-color: $yellow; } }
		.exhibiting_padTop & {
			padding-top: 86px;
			overflow: visible;
			@include lg {
				padding-top: 10px;
				padding-bottom: 48px; }
			@include sm {
				padding-top: 30px;
				padding-bottom: 40px; } } }
	&__serial-number {
		position: absolute;
		top: 182px;
		left: 0;
		right: 0;
		padding-left: 28%;
		letter-spacing: -0.064em;
		color: $white;
		@include lg {
			display: none; }
		span {
			min-width: 350px;
			display: inline-block; } }
	&__top {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		height: 42px;
		font-size: 0;
		margin: 0 auto;
		max-width: 980px;
		text-align: right;
		&:before {
			content: "";
			position: absolute;
			left: 0;
			right: 0;
			top: 0;
			bottom: 0;
			background: $yellow;
			transform: skewY(2deg);
			transform-origin: right bottom; }
		i {
			position: relative;
			z-index: 1;
			display: inline-block;
			vertical-align: top;
			margin: -15px 10px 0 0;
			border-radius: 50%;
			width: 20px;
			height: 20px;
			background: $yellow; } }
	&__bottom {
		position: absolute;
		bottom: 0;
		left: 0;
		right: 0;
		padding: 0 15px;
		height: 42px;
		font-size: 0;
		&:before {
			content: "";
			position: absolute;
			left: 0;
			right: 0;
			top: 0;
			bottom: 0;
			background: $yellow;
			transform: skewY(-2deg);
			transform-origin: left bottom; }
		i {
			position: relative;
			z-index: 1;
			display: inline-block;
			vertical-align: top;
			margin: 25px 10px 0 0;
			border-radius: 50%;
			width: 20px;
			height: 20px;
			background: $black; } }
	&__inner {
		padding-bottom: 45px;
		@include clr;
		@include lg {
			position: static; }
		@include sm {
			padding-bottom: 20px; } }
	&__left {
		float: left;
		padding-right: 75px;
		width: 57.5%;
		@include lg {
			padding-right: 0;
			width: 100%; }
		.exhibiting_yellow & {
			width: 66.5%;
			@include lg {
				width: 100%; } } }
	&__right {
		float: right;
		font-size: 35px;
		width: 42.5%;
		color: $yellow;
		@include lg {
			padding-left: 35px;
			width: 100%;
			font-size: 40px;
			text-transform: uppercase; }
		@include sm {
			padding-left: 20px;
			font-size: 20px; }
		.exhibiting_yellow & {
			width: 33.5%;
			color: $black;
			@include lg {
				width: 100%; } } }
	&__title {
		position: relative;
		padding: 0 0 35px 0;
		@include f('light', 42);
		line-height: 1.05;
		@include lg {
			padding-bottom: 40px;
			font-size: 53px; }
		@include sm {
			font-size: 33px; }
		.exhibiting_yellow & {
			padding-right: 30px;
			@include lg {
				padding-right: 0; }
			.icon {
				fill: $black; } }
		.icon {
			position: absolute;
			left: 0;
			bottom: 0;
			fill: $yellow;
			font-size: 17px;
			@include lg {
				font-size: 28px; }
			.exhibiting_yellow & {
				color: $black; } } }
	&__list {
		&:not(:first-child) {
			margin-top: 55px;
			@include lg {
				margin-top: 43px; }
			@include sm {
				margin-top: 20px; } } }
	&__img {
		margin-top: -117px;
		margin-left: -91px;
		font-size: 0;
		img {
			display: block;
			@include lg {
				max-width: 100%;
				max-height: 100%; } }
		&_clock {
			@include lg {
				right: -65px;
				bottom: 69px;
				height: 259px; }
			@include sm {
				bottom: 55px;
				height: 180px; } }
		&_calculator {
			margin: -185px 0 0 -60px;
			@include lg {
				right: 44px;
				bottom: -103px;
				height: 444px;
				img {
					height: 100%; } }
			@include sm {
				right: 10px;
				left: 10px;
				bottom: -50px;
				height: 300px; } }
		&_hourglass {
			margin: -132px 0 -161px 45px;
			@include lg {
				margin: 0;
				right: -218px;
				bottom: 170px;
				width: 468px;
				transform: rotate(36deg);
				img {
					width: 100%; } }
			@include sm {
				right: -78px;
				bottom: 102px;
				width: 170px; } }
		@include lg {
			position: absolute;
			margin: 0; } }
	&__item {
		position: relative;
		padding-left: 17px;
		@include lg {
			padding-left: 35px;
			letter-spacing: -0.012em; }
		@include sm {
			padding-left: 17px;
			font-size: 20px; }
		&:not(:first-child) {
			margin-top: 20px;
			@include lg {
				margin-top: 20px; } }
		&:before {
			position: absolute;
			content: '';
			left: 0;
			top: 7px;
			width: 9px;
			height: 9px;
			background: $yellow;
			@include lg {
				height: 18px;
				width: 18px; }
			@include sm {
				top: 4px;
				height: 9px;
				width: 9px; }
			.exhibiting_yellow & {
				background: $black; } } }
	&__title-bg {
		margin: 32px 0 13px;
		padding: 5px 10px 0;
		@include f('light', 25);
		color: $black;
		vertical-align: top;
		display: inline-block;
		background: $yellow;
		@include lg {
			margin-top: 60px;
			margin-bottom: 20px;
			padding: 12px 10px 5px;
			font-size: 34px; }
		@include sm {
			margin-top: 30px;
			font-size: 17px; }
		.exhibiting_yellow & {
			padding: 10px 10px 4px;
			color: $white;
			background: $black; } }
	&__title-sm {
		@include f('light', 60);
		@include lg {
			font-size: 40px;
			vertical-align: top;
			display: inline-block; }
		@include sm {
			font-size: 20px; } }
	&__title-md {
		@include f('light', 80);
		color: $white;
		letter-spacing: -0.017em;
		.exhibiting_yellow & {
			color: $black; }
		@include lg {
			font-size: 112px;
			.exhibiting_yellow & {
				font-size: 40px; } }
		@include sm {
			font-size: 66px;
			.exhibiting_yellow & {
				font-size: 20px; } } }
	br {
		@include lg {
			display: none; } }
	.btn {
		&:not(:first-child) {
			margin-top: 40px; } } }
